<template>
	<view>
		<navbar v-model="screenHeight" :iSimmersive="false" :placeholder="true" title="推广统计"></navbar>
		<view class="top">
			<text :class="{active: isActive == 1}" @click="changeData(1)">一级会员</text>
			<text :class="{active: isActive == 2}" @click="changeData(2)">二级会员</text>
		</view>
		<view class="top-placeholder"></view>
		
		<view class="list-wrap">
			<mescroll-body 
			ref="mescrollRef" 
			@init="mescrollInit" 
			@down="downCallback" 
			@up="upCallback"
			:height="screenHeight+'px'"
			>
				<view class="list-box">
					<view class="list-item" v-for="(item, index) in result" :key="index">
						<view class="item-box">
							<view class="avatar">
								<image :src="item.avatar"></image>
							</view>
							<view class="ll">
								<text>{{item.nickname}}</text>
								<text>{{item.time}}</text>
							</view>
							<view class="rr">
								<text>{{item.order_count}} 单</text>
								<text>{{item.sum_order_amount}} 元</text>
							</view>
						</view>
					</view>
				</view>
			</mescroll-body>
		</view>
	</view>
</template>

<script>
	import MescrollMixin from "@/uni_modules/mescroll-uni/components/mescroll-uni/mescroll-mixins.js";
	import navbar from "@/components/navbar/navbar";
	export default {
		mixins: [MescrollMixin],
		components: {
			navbar
		},
		data(){
			return {
				screenHeight: 0,
				isActive:1,
				result: []
			};
		},
		methods: {
			changeData(index){
				this.isActive = index;
				this.downCallback();
			},
			downCallback(){
				setTimeout(()=>{
					this.mescroll.resetUpScroll();
				},1200);
			},
			triggerDownScroll(){
				this.mescroll.triggerDownScroll();
			},
			upCallback(page) {
				this.$http.getSpreadPromotionList({
					type: this.isActive,
					page: page.num
				}).then((result) => {
					this.mescroll.endByPage(result.data.list.length, result.data.total);
					if(result.status==1){
						if(page.num == 1) this.result = [];
						this.result = this.result.concat(result.data.list);
					}else if(result.status == -1){
						this.mescroll.endErr();
					}
				}).catch((error) => {
					this.mescroll.endErr();
				});
			},
		}
	}
</script>

